

.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
  }
  
  .Icon-container-check {
    position: absolute;
    left: 12px;
    color: var(--check-icon-color);
    transition: color 0.3s ease-in-out; /* Aplicamos la transición aquí */
  }
  
  .Icon-container-check--active {
    color: #15db5e; /* Cambiamos el color a blanco cuando está activo */
  }
  
  .Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  
  .Icon-container-delete:hover {
    color: red

   
  }
  .Icon-svg{
    width:24px;
    height:24px;
    
  }
  .Icon-container-check .Icon-svg {
    fill: green;
}

  .Icon-container-delete:hover .Icon-svg {
    fill: red;
}