.CreateTodoButton{
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;

    transform: rorate(0);
    transform: .3 ease;
}
.CreateTodoButton:hover{
    transform: rotate(224deg);
}